import React, { useContext, useState } from 'react'
import { Dialog, DialogTitle, Grid, Divider, Button } from '@material-ui/core'
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab'
import { RoomContext } from '../../RoomContext'

function OutletPropertiesMenu() {

    const { __outletPropertiesMenu, __rooms } = useContext(RoomContext)

    const [outletPropertiesMenu, setOutletPropertiesMenu] = __outletPropertiesMenu
    const [rooms, setRooms] = __rooms

    const { open, outlet } = outletPropertiesMenu

    const [thisOutlet, setThisOutlet] = useState({
        type : 1,
        grounding : true,
        shutter : true
    })

    const handleClose = () => {
        setOutletPropertiesMenu(prev => { return { ...prev, open: false } })
    }

    return (
        <Dialog aria-labelledby="simple-dialog-title" open={open} onClose={handleClose}>
            <DialogTitle id="simple-dialog-title">Set outlet properties</DialogTitle>

            <Grid container direction='column' spacing={1}>
                <Grid item>
                    <Grid
                        container
                        direction='column'
                    >
                        <Grid item xs={12}>
                            <ToggleButtonGroup
                                exclusive
                                aria-label="text alignment"
                            >
                                <ToggleButton selected={thisOutlet.type === 1}>ONE</ToggleButton>
                                <ToggleButton selected={thisOutlet.type === 2}>TWO</ToggleButton>
                                <ToggleButton selected={thisOutlet.type === 3}>THREE</ToggleButton>
                                <ToggleButton selected={thisOutlet.type === 4}>FOOR</ToggleButton>
                                <ToggleButton selected={thisOutlet.type === 5}>FIVE</ToggleButton>
                            </ToggleButtonGroup>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item>
                    <Grid container justify="center" spacing={0}>

                        <Grid item xs={6} style={{ textAlign: "center" }} >
                            <ToggleButton selected={thisOutlet.grounding}>Grounding</ToggleButton>
                        </Grid>
                        <Grid item xs={6} style={{ textAlign: "center" }} >
                            <ToggleButton selected={thisOutlet.shutter}>Shutter</ToggleButton>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item>
                    <Button variant="contained" color="primary" fullWidth>
                        Save
                    </Button>
                </Grid>
            </Grid>

        </Dialog>
    )
}

export default OutletPropertiesMenu
